.go {
  width: 136px;
  height: 43px;
  background-color: #c7e957;
  color: #000;
  font-size: 20px;
  text-align: center;
  border-radius: 80px;
  position: absolute;
  left: 30px;
  z-index: 2;
  bottom: 27px;
}

.go:hover {
  background-color: #daf289;
}

.go:active {
  background-color: #ebffab;
}
